B;EACjB,iBAAiB;AACnB;;AAEA;EACE,oBAAoB;EACpB,qBAAqB;EACrB,gBAAgB;AAClB;;AAEA;EACE,0BAA0B;AAC5B;;AAEA;EACE,iBAAiB;EACjB,mBAAmB;EACnB,iBAAiB;AACnB;;AAEA;EACE,gBAAgB;AAClB;;AAEA;EACE,mBAAmB;AACrB;;AAEA;EACE,aAAa;EACb,sBAAsB;EACtB,SAAS;AACX;;AAEA;EACE,aAAa;EACb,+BAA+B;EAC/B,kBAAkB;EAClB,mCAAmC;EACnC;;mBAEiB;EACjB,oBAAoB,EAAE,iBAAiB;AACzC;;AAEA;EACE,0BAA0B;EAC1B,yCAAyC;AAC3C;;AAEA;EACE,cAAc;EACd,WAAW,EAAE,gBAAgB;EAC7B,kBAAkB;EAClB,gBAAgB;EAChB,6BAA6B;AAC/B;;AAEA;EACE,YAAY;EACZ,sBAAsB;AACxB;;AAEA;EACE,WAAW;EACX,YAAY;EACZ,iBAAiB;EACjB,cAAc;AAChB;;AAEA;EACE,OAAO;EACP,aAAa;EACb,sBAAsB;EACtB,YAAY;EACZ,YAAY;AACd;;AAEA;EACE,kBAAkB;EAClB,iBAAiB;EACjB,kBAAkB;EAClB,mBAAmB;EACnB,gBAAgB;EAChB,uBAAuB;AACzB;;AAEA;EACE,kBAAkB;EAClB,qBAAqB;AACvB;;AAEA;EACE,0BAA0B;AAC5B;;AAEA;EACE,oBAAoB;EACpB,iBAAiB;EACjB,kBAAkB;AACpB;;AAEA;EACE,qBAAqB;EACrB,kBAAkB;EAClB,mBAAmB;AACrB;;AAEA;EACE,wBAAwB;EACxB,gBAAgB;EAChB,mBAAmB;EACnB,OAAO;AACT;;AAEA;EACE,aAAa;EACb,SAAS;EACT,mBAAmB;EACnB,mBAAmB;AACrB;;AAEA;EACE,mBAAmB;EACnB,iBAAiB;AACnB;;AAEA;EACE,qBAAqB;EACrB,kBAAkB;AACpB;;AAEA;EACE,aAAa;EACb,oBAAoB;EACpB,qBAAqB;EACrB,iBAAiB;EACjB,gBAAgB;EAChB,+BAA+B;EAC/B,kBAAkB;EAClB,oBAAoB;EACpB,wBAAwB;EACxB,mBAAmB;AACrB;;AAEA;EACE,yBAAyB;EACzB,wBAAwB;AAC1B;;AAEA;EACE,mBAAmB;AACrB;;AAEA;EACE;IACE,sBAAsB;EACxB;;EAEA;IACE,WAAW;IACX,aAAa;EACf;AACF;AACA;EACE,aAAa;EACb,eAAe;EACf,MAAM;EACN,8BAA8B;EAC9B,2BAA2B;AAC7B","sourcesContent":["/* ============================= */\n/* CSS CUSTOM PROPERTIES (ROOT)  */\n/* ============================= */\n:root {\n  /* Background Colors */\n  --background: #221a0f;\n  --lighter-background: #362712;\n  --selection: #362712;\n  --current-line: #2b221a;\n\n  /* Foreground/Text Colors */\n  --foreground: #d3af86;\n  --comment: #a57a4c;\n\n  /* Syntax Colors */\n  --red: #dc3958;\n  --orange: #f79a32;\n  --yellow: #f06431;\n  --green: #889b4a;\n  --cyan: #088649;\n  --blue: #7e5f8a;\n  --purple: #98676a;\n\n  /* UI Elements */\n  --gutter: #362712;\n  --guide: #2b221a;\n}\n/* ============================= */\n/* RESET + BASE STYLES           */\n/* ============================= */\n* {\n  margin: 0;\n  padding: 0;\n  box-sizing: border-box;\n}\n\nbody {\n  background: var(--background);\n  color: var(--foreground);\n  font-family: 'Courier New', monospace;\n  font-size: 14px;\n  line-height: 1.6;\n  text-size-adjust: 100%;\n  min-height: 100vh;\n}\n\n/* CRT Scanline Effect */\nbody:not(.no-scanlines)::after {\n  content: '';\n  position: fixed;\n  top: 0;\n  left: 0;\n  width: 100%;\n  height: 100%;\n  background-image: linear-gradient(rgba(0, 0, 0, 0.4) 1px, transparent 1px);\n  background-size: 2px 2px;\n  background-repeat: repeat;\n  pointer-events: none;\n  z-index: 9999;\n}\n\n/* ============================= */\n/* NAVIGATION                    */\n/* ============================= */\nheader {\n  background: var(--current-line);\n  padding: 15px 20px;\n  display: flex;\n  justify-content: space-between;\n  gap: 25px;\n  margin-bottom: 40px;\n  align-items: center;\n}\n\nnav a {\n  color: var(--comment);\n  text-decoration: underline;\n  transition: color 0.2s;\n}\n\nnav a:hover {\n  color: var(--cyan);\n}\n\n/* ============================= */\n/* LAYOUT                        */\n/* ============================= */\n.container {\n  max-width: 800px;\n  margin: 0 auto;\n  padding: 20px 10px;\n}\n\n.profile-section {\n  display: flex;\n  align-items: center;\n  gap: 1rem;\n  margin-bottom: 40px;\n  padding-bottom: 1rem;\n  border-bottom: 1px solid var(--blue);\n}\n\n.profile-details {\n  flex: 1;\n}\n\n/* ============================= */\n/* TYPOGRAPHY                    */\n/* ============================= */\nh1 {\n  font-family: 'Berkeley Mono', monospace;\n  font-weight: 700;\n  font-size: 2.25rem;\n  font-style: italic;\n  color: var(--red);\n}\n\nh2 {\n  font-size: 16px;\n  margin: 30px 0 15px;\n  color: var(--cyan);\n}\n\n.subtitle,\n.status,\n.reading {\n  color: var(--comment);\n}\n\n.subtitle {\n  margin-bottom: 3rem;\n}\n\n.status,\n.reading {\n  font-style: italic;\n}\n\n.bio {\n  margin-bottom: 0.5rem;\n}\n\n/* ============================= */\n/* CONTENT BLOCKS                */\n/* ============================= */\n.section {\n  margin-bottom: 30px;\n}\n\n.item {\n  margin-bottom: 20px;\n}\n\n.item-header {\n  display: flex;\n  justify-content: space-between;\n  margin-bottom: 5px;\n}\n\n.company {\n  color: var(--cyan);\n  text-decoration: none;\n}\n\n.company:hover {\n  text-decoration: underline;\n}\n\n.role {\n  color: var(--comment);\n}\n\n.dates {\n  color: var(--comment);\n}\n\n.description {\n  color: var(--blue);\n  padding-left: 10px;\n}\n\n/* ============================= */\n/* STATS GRID                    */\n/* ============================= */\n.stats {\n  display: grid;\n  grid-template-columns: repeat(2, 1fr);\n  gap: 15px;\n  margin-top: 10px;\n}\n\n.stat-item {\n  display: flex;\n  justify-content: space-between;\n  padding: 8px;\n  background: var(--current-line);\n  border-radius: 4px;\n}\n\n.stat-label {\n  color: var(--comment);\n}\n\n.stat-value {\n  color: var(--green);\n  font-weight: bold;\n}\n\n/* ============================= */\n/* PRE / ASCII ART               */\n/* ============================= */\npre {\n  font-size: 4px;\n  line-height: 1;\n  white-space: pre;\n  flex-shrink: 0;\n}\n\n/* ============================= */\n/* LINK STYLES                   */\n/* ============================= */\na {\n  color: var(--cyan);\n  text-decoration: none;\n}\n\na:hover {\n  text-decoration: underline;\n}\n\n/* ============================= */\n/* CONTACT BUTTON SECTION        */\n/* ============================= */\n.contact {\n  display: flex;\n  align-items: center;\n  gap: 12px;\n  margin-right: 1rem;\n}\n\n.contact button {\n  background: var(--current-line);\n  border: 1px solid var(--guide);\n  padding: 6px 10px;\n  border-radius: 6px;\n  cursor: pointer;\n  transition:\n    background 0.2s ease,\n    border-color 0.2s ease;\n}\n\n.contact button:hover {\n  background: var(--lighter-background);\n  border-color: var(--cyan);\n}\n\n/* Remove button default styles */\n.contact button a {\n  display: flex;\n  align-items: center;\n  gap: 5px;\n  text-decoration: none;\n  color: var(--foreground);\n  font-size: 13px;\n}\n\n/* SVG ICONS */\n.contact svg {\n  width: 18px;\n  height: 18px;\n  filter: brightness(0) saturate(100%) invert(88%) sepia(0%) saturate(0%);\n  transition: filter 0.2s ease;\n}\n\n.contact button:hover svg {\n  fill: var(--cyan);\n}\n\n/* The \"X\" link styling */\n.contact a {\n  text-decoration: none;\n  color: var(--foreground);\n  font-size: 14px;\n  font-weight: bold;\n}\n\n.contact button:hover a {\n  color: var(--cyan);\n}\n\n/* ============================= */\n/* RESPONSIVE                    */\n/* ============================= */\n@media (max-width: 700px) {\n  header {\n    flex-direction: column;\n    gap: 15px;\n  }\n\n  header nav {\n    margin-left: 0;\n  }\n\n  .contact {\n    margin-right: 0;\n  }\n}\n\n.status {\n  display: flex;\n  align-items: center;\n  gap: 8px;\n  margin-bottom: 0.5rem;\n}\n\n.status-dot {\n  width: 8px;\n  height: 8px;\n  border-radius: 50%;\n  background-color: var(--green);\n  position: relative;\n  animation: pulse 2s ease-in-out infinite;\n}\n\n/* Pulsing animation */\n@keyframes pulse {\n  0%,\n  100% {\n    opacity: 1;\n    transform: scale(1);\n  }\n  50% {\n    opacity: 0.6;\n    transform: scale(1.1);\n  }\n}\n\n/* Optional: Add a ring effect */\n.status-dot::before {\n  content: '';\n  position: absolute;\n  top: -4px;\n  left: -4px;\n  right: -4px;\n  bottom: -4px;\n  border-radius: 50%;\n  border: 2px solid var(--green);\n  opacity: 0;\n  animation: ping 2s ease-in-out infinite;\n}\n\n@keyframes ping {\n  0% {\n    transform: scale(1);\n    opacity: 0.8;\n  }\n  100% {\n    transform: scale(1.5);\n    opacity: 0;\n  }\n}\n\n.studying-link {\n  color: var(--yellow);\n  text-decoration: underline;\n  font-style: italic;\n}\n\n.studying-link:hover {\n  color: var(--orange);\n}\n\n.company.acquired {\n  color: var(--red);\n  font-weight: bold;\n}\n\n.company.acquired:hover {\n  text-decoration: underline;\n}\n\n.company.acquired::after {\n  content: ' (acquired)';\n  color: var(--red);\n  opacity: 0.7;\n  font-style: italic;\n}\n\n/* Add this to your existing styles.css */\n\n/* Smooth transition for status text */\n#status-text {\n  transition: opacity 0.25s ease-in-out;\n}\n\n/* Smooth transition for status dot color changes */\n.status-dot {\n  transition: background-color 0.5s ease-in-out;\n}\n\n/* Optional: smoother pulse animation timing */\n@keyframes pulse {\n  0%,\n  100% {\n    opacity: 1;\n    transform: scale(1);\n  }\n  50% {\n    opacity: 0.6;\n    transform: scale(1.1);\n  }\n}\n\n.coding-stats-section {\n  margin-top: 40px;\n}\n\n.section-title {\n  color: var(--cyan);\n  font-size: 16px;\n  margin-bottom: 15px;\n}\n\n.week-label {\n  color: var(--comment);\n  font-style: italic;\n  margin-left: 8px;\n  font-size: 14px;\n}\n\n.stats-grid {\n  display: grid;\n  grid-template-columns: repeat(4, 1fr);\n  gap: 15px;\n  margin-bottom: 30px;\n}\n\n.stat-card {\n  background: var(--current-line);\n  border: 1px solid var(--yellow);\n  border-radius: 8px;\n  padding: 15px;\n}\n\n.stat-label {\n  color: var(--comment);\n  font-size: 12px;\n  margin-bottom: 8px;\n}\n\n.stat-value {\n  color: var(--foreground);\n  font-size: 24px;\n  font-weight: bold;\n}\n\n.language-breakdown {\n  background: var(--current-line);\n  padding: 20px;\n  border-radius: 8px;\n  margin-top: 20px;\n}\n\n.breakdown-header,\n.project-header {\n  color: var(--comment);\n  font-size: 14px;\n  margin-bottom: 20px;\n  cursor: pointer;\n  user-select: none;\n}\n\n.breakdown-header:hover,\n.project-header:hover {\n  color: var(--cyan);\n}\n\n.language-item {\n  margin-bottom: 15px;\n}\n\n.language-header {\n  display: flex;\n  justify-content: space-between;\n  margin-bottom: 5px;\n}\n\n.language-name {\n  color: var(--foreground);\n  font-weight: bold;\n}\n\n.language-stats {\n  color: var(--comment);\n  font-size: 12px;\n}\n\n.progress-bar {\n  height: 6px;\n  background: var(--background);\n  border-radius: 3px;\n  overflow: hidden;\n}\n\n.progress-fill {\n  height: 100%;\n  border-radius: 3px;\n  transition: width 0.3s ease;\n}\n\n.loading {\n  color: var(--comment);\n  font-style: italic;\n  text-align: center;\n  padding: 20px;\n}\n\n@media (max-width: 700px) {\n  .stats-grid {\n    grid-template-columns: repeat(2, 1fr);\n  }\n}\n\n.quote-section {\n  text-align: center;\n}\n.quote-text {\n  font-size: 1rem;\n  color: var(--text);\n  font-style: italic;\n  font-family: 'Courier New', monospace;\n  border-right: 2px solid var(--text);\n  white-space: nowrap;\n  overflow: hidden;\n  display: inline-block;\n  animation:\n    typing 8s steps(30, end) infinite,\n    blink 0.75s step-end infinite;\n}\n\n@keyframes typing {\n  0% {\n    width: 0;\n  }\n  40% {\n    width: 100%;\n  }\n  60% {\n    width: 100%;\n  }\n  100% {\n    width: 0;\n  }\n}\n\n@keyframes blink {\n  from,\n  to {\n    border-color: transparent;\n  }\n  50% {\n    border-color: var(--text);\n  }\n}\n\n/* Projects Page */\n.projects-grid {\n  display: grid;\n  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));\n  gap: 20px;\n  margin-top: 30px;\n}\n\n.project-card {\n  background: var(--current-line);\n  border: 1px solid var(--guide);\n  border-radius: 8px;\n  padding: 20px;\n  transition: border-color 0.3s ease;\n}\n\n.project-card:hover {\n  border-color: var(--cyan);\n}\n\n.project-header {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  margin-bottom: 15px;\n}\n\n.project-status {\n  font-size: 11px;\n  padding: 4px 8px;\n  border-radius: 4px;\n  background: var(--green);\n  color: var(--background);\n}\n\n.project-status.archived {\n  background: var(--comment);\n}\n\n.project-description {\n  color: var(--comment);\n  margin-bottom: 15px;\n  line-height: 1.6;\n}\n\n.project-tech {\n  di