lor: var(--comment);\n  font-style: italic;\n  margin-left: 5px;\n}\n\n/* Responsive Design */\n@media (max-width: 700px) {\n  .learning-header {\n    flex-direction: column;\n    align-items: flex-start;\n    gap: 10px;\n  }\n\n  .course-header {\n    flex-direction: column;\n    align-items: flex-start;\n    gap: 5px;\n  }\n\n  .course-title {\n    min-width: 100%;\n  }\n\n  .course-time {\n    margin-left: 0;\n  }\n}\n\n.music-player {\n  position: fixed;\n  bottom: 20px;\n  right: 20px;\n  width: 60px;\n  height: 60px;\n  border-radius: 50%;\n  background: var(--current-line);\n  border: 2px solid var(--comment);\n  cursor: pointer;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  transition: all 0.3s ease;\n  z-index: 1000;\n}\n\n.music-player:hover {\n  border-color: var(--cyan);\n  transform: scale(1.05);\n}\n\n.cd-icon {\n  position: absolute;\n  opacity: 0.3;\n  transition: all 0.3s ease;\n}\n\n.music-player.playing .cd-icon {\n  animation: spin 3s linear infinite;\n  opacity: 0.5;\n}\n\n@keyframes spin {\n  from {\n    transform: rotate(0deg);\n  }\n  to {\n    transform: rotate(360deg);\n  }\n}\n\n.play-icon,\n.pause-icon {\n  width: 24px;\n  height: 24px;\n  color: var(--foreground);\n  position: absolute;\n  transition: opacity 0.2s ease;\n}\n\n.pause-icon {\n  opacity: 0;\n}\n\n.music-player.playing .play-icon {\n  opacity: 0;\n}\n\n.music-player.playing .pause-icon {\n  opacity: 1;\n}\n\n/* Blog Page Styles */\n.blog-container {\n  max-width: 800px;\n  margin: 0 auto;\n}\n\n.blog-post {\n  background: rgba(40, 42, 54, 0.5);\n  border: 1px solid var(--current-line);\n  border-radius: 8px;\n  padding: 25px;\n  margin-bottom: 30px;\n  transition: all 0.3s ease;\n}\n\n.blog-post:hover {\n  background: rgba(40, 42, 54, 0.7);\n  border-color: var(--purple);\n  transform: translateY(-2px);\n}\n\n.post-header h2 {\n  margin: 0 0 10px 0;\n  font-size: 1.5rem;\n}\n\n.post-meta {\n  display: flex;\n  align-items: center;\n  gap: 10px;\n  